.header {
  padding: 10px 0;
  background-image: url("../img/header-bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.e-ticket {
  padding-top: 20px;
}

.e-ticket .col {
  display: flex;
  flex-direction: column;
}

.e-ticket .col:not(:last-child) {
  margin-right: 20px;
}

.e-ticket__top {
  margin-bottom: 30px;
}

.e-ticket__heading {
  font-family: Montserrat-SemiBold, sans-serif;
  font-weight: 600;
  font-size: 14px;
}

.e-ticket__gray {
  font-family: Montserrat-Medium, sans-serif;
  font-weight: 500;
  color: #828282;
}

.e-ticket__bold {
  padding-top: 5px;
  font-size: 12px;
  margin-top: auto;
}

.e-ticket__bottom {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}

.e-ticket__booking-ref {
  width: 98px;
}

.e-ticket__online-ref {
  width: 210px;
}

.e-ticket__iin {
  width: 80px;
}

.e-ticket__iata {
  width: 55px;
}

.e-ticket img {
  margin-top: 10px;
  margin-bottom: 15px;
}

.e-ticket p {
  font-family: Montserrat-Medium, sans-serif;
  font-weight: 500;
}

p {
  font-family: Montserrat-Medium, sans-serif;
  font-weight: 500;
}

.bold {
  padding-top: 5px;
  font-size: 9px;
  font-family: Montserrat-SemiBold, sans-serif;
  font-weight: 600;
}

.col {
  display: flex;
  flex-direction: column;
}

.col .bold {
  margin-top: auto;
}

.flight-data__title {
  margin-bottom: 5px;
  font-size: 10px;
  font-family: Montserrat-SemiBold, sans-serif;
  font-weight: 600;
}

.flight-data__top {
  display: flex;
  margin-bottom: 20px;
  justify-content: space-between;
}

.flight-data__top p {
  font-family: Montserrat-Medium, sans-serif;
  font-weight: 500;
}

.flight-data__top .bold {
  font-size: 9px;
  font-family: Montserrat-SemiBold, sans-serif;
  font-weight: 600;
}

.flight-data__top .col:not(:last-child) {
  margin-right: 20px;
}

.flight-data__top .first {
  width: 120px;
}

.flight-data__top .second {
  width: 150px;
}

.flight-data__top .third {
  width: 126px;
}

.flight-data__top .fourth {
  width: 100px;
}

.flight-data__bottom {
  display: flex;
  margin-bottom: 25px;
}

.flight-data__bottom .first {
  width: 120px;
}

.flight-data__bottom .second {
  width: 150px;
}

.flight-data__bottom .third {
  width: 128px;
}

.flight-data__bottom .col:not(:last-child) {
  margin-right: 20px;
}

.details__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.details__top .col:not(:last-child) {
  margin-right: 20px;
}

.details__top .first {
  width: 120px;
}

.details__top .second {
  width: 140px;
}

.details__top .third {
  width: 80px;
}

.details__top .fourth {
  width: 80px;
}

.details__top .fifth {
  width: 80px;
}

.details__middle {
  display: flex;
  justify-content: space-between;
}

.details__middle h2 {
  font-size: 10px;
}

.details__bottom {
  display: flex;
  margin-bottom: 24px;
}

.details__bottom .col:not(:last-child) {
  margin-right: 20px;
}

.details__bottom .first {
  width: 120px;
}

.details__bottom .second {
  width: 140px;
}

.details__bottom .third {
  width: 80px;
}

.details__bottom .fourth {
  width: 80px;
}

.details__bottom .fifth {
  width: 85px;
}

.divider {
  width: 100%;
}

.footer {
  background-image: url("../img/footer-bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 15px 0;
}

.footer .container {
  display: flex;
}

.footer img {
  display: inline-block;
  margin-right: 40px;
}